.slideshow-container {
    position: relative;
    max-width: 1000px;
    margin: auto;
    display: flex;
    justify-content: space-between;
}

.prev,
.next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    margin-top: -20px;
    padding: 1.6%;
    color: white;
    font-weight: bold;
    font-size: 18px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    background-color: rgb(192, 165, 220);
}

.next {
    right: 0px;
    border-radius: 3px 0 0 3px;
}

.prev:hover,
.next:hover {
    // background-color: rgba(0, 0, 0, 0, 0.8);
    background-color: rgb(192, 165, 220);
}

.text {
    color: #f2f2f2;
    font-size: 12px;
    padding: 8px 12px;
    position: absolute;
    top: 0px;
}

.dot {
    cursor: pointer;
    height: 13px;
    width: 13px;
    margin: 0px 2px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
}

.active,
dot:hover {
    background-color: #717171;
}

.fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
}

@keyframes fade {
    from {
        opacity: .4;
    }

    to {
        opacity: 1;
    }
}